.music-list-wrapper {
	margin: 0 auto;
    height: auto;
    width: 300px;
    position: relative;
    display: flex;
    flex-direction: column;
}
.artwork {
	background: url('../assets/dawn-artwork.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	width: 300px;
	height: 300px;
}
.heading {
	background-color: #000000;
	color: #FFFFFF;
	text-align: center;
	height: 80px;
	position: relative;
}
.heading::after {
	content: "";
    display: inline-block;
    left: 50%;
    margin-left: -12px;
    margin-bottom: -12px;
    width: 0;
    height: 0;
    bottom: 0;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 12px solid #181818;
    position: absolute;
}
.heading > h1 {
	font-size: 18px;
    font-weight: 500;
    line-height: 18px;
    margin-top: 18px;
}
.heading > p {
	margin: 5px 0 0;
    font-size: 14px;
}
.music-services-wrapper {
	display: flex;
	flex-direction: column;
}
.music-service {
	display: flex;
    justify-content: space-between;
    background-color: #FFFFFF;
    cursor: pointer;
    border-radius: initial;
    box-shadow: inset 0 -2px 9px 0 rgb(0 0 0 / 6%);
    text-decoration: none;
    transition: background-color .3s ease,box-shadow .3s ease;
    padding: 16px 12px;
    height: 45px;
}
